<template><section>

<h2>Icon 图标</h2>
<p>提供了一套常用的图标集合。</p>
<h3>使用方法</h3>
<p>直接通过name属性来使用即可。例如：</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;/head&gt;&lt;body&gt;&lt;byt-icon name=\&quot;share\&quot;&gt;&lt;/byt-icon&gt;\n&lt;byt-button type=\&quot;primary\&quot; icon=\&quot;byt-icon-search\&quot;&gt;搜索&lt;/byt-button&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                        <div slot="desc"><html><head></head><body><byt-icon name="share"></byt-icon>
<byt-button type="primary" icon="byt-icon-search">搜索</byt-button>

</body></html></div>
                        
                        <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">byt-icon</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"share"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">byt-icon</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">byt-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"byt-icon-search"</span>&gt;</span>搜索<span class="hljs-tag">&lt;/<span class="hljs-name">byt-button</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>图标集合</h3>
<ul class="icons-list clearfix">
  <li v-for="(name,index) in icons" :key="index">
    <span>
      <byt-icon :name="name" color="deeppink" size="30px"></byt-icon>
      <span class="icon-name">{{'byt-icon-' + name}}</span>
    </span>
  </li>
</ul>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>字体名称</td>
<td>string</td>
<td>any</td>
<td>——</td>
</tr>
<tr>
<td>color</td>
<td>改变字体颜色</td>
<td>string</td>
<td>any</td>
<td>deeppink</td>
</tr>
<tr>
<td>size</td>
<td>改变字体大小</td>
<td>string</td>
<td>any</td>
<td>16px</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="scss">
  .demo-icon .source > i {
    color: #606266;
    margin: 0 20px;
    font-size: 1.5em;
    vertical-align: middle;
  }
  
  .demo-icon .source > button {
    margin: 0 20px;
  }

  .byt-doc-content section ul.icons-list {
    overflow: hidden;
    list-style: none;
    padding: 0;
    background:#fff;
    border: solid 1px #eaeefb;
    border-radius: 4px;
  }
  .byt-doc-content section ul.icons-list li {
    margin:0;
    float: left;
    width: 16.66%;
    text-align: center;
    height: 120px;
    line-height: 120px;
    color: #666;
    font-size: 13px;
    transition: color .15s linear;
    border-right: 1px solid #eee;
    border-bottom: 1px solid #eee;
    margin-right: -1px;
    margin-bottom: -1px;
    @utils-vertical-center;
    &:after {
      height:0;
    }
    span {
      display: inline-block;
      line-height: normal;
      vertical-align: middle;
      font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;
      color: #99a9bf;
    }
    
    i {
      display: block;
      font-size: 32px;
      margin-bottom: 15px;
      color: #606266;
    }
    
    .icon-name {
      display: inline-block;
      padding: 0 3px;
      height: 1em;
      color: #606266;
    }
    
    &:hover {
      color: rgb(92, 182, 255);
    }
  }
</style>
<script>
  var iconList = require('../icon.json');

  export default {
    data() {
      return {
        icons: iconList
      };
    }
  }
</script>


